@use './colors';


%common-border {
   border-radius: 16px;
}

%common-border-button {
   border: 3px solid colors.$primary-main;
   border-radius: 16px;
}

%common-center {
   display: grid;
   place-items: center;
}

%common-shadow {
   box-shadow: 0px 0px 15px rgba(255, 197, 0, 0.5);
}

%common-shadow-onhover {
   box-shadow: 0px 0px 25px rgba(255, 197, 0, 0.7);
}

%common-border-button-green {
   border: 3px solid colors.$secondary-green;
   border-radius: 16px;
}

%common-shadow-green {
   border: 3px solid colors.$primary-background;
   box-shadow: 0px 0px 15px #3E8118;
}

%common-shadow-green-onhover {
   box-shadow: 0px 0px 25px #3E8118;
}

%background-image {
   background-image: url('../static/assets/ui/QuizBackground1.jpg');
}